<template>
    <div class="unfinishedanswer center">
         <div class="center_breadcrumb">
            <crumb></crumb>
        </div>
        <el-row>
                <div class="ability-query_side">
                    <h3>问卷类型</h3>
                    <ul>
                        <li
                         :class="{sideActive:sideActiveIndex === index}"
                        @click="sideClick(vo.id, index) "
                        v-for="(vo, index) in classifyOptions"
                        :key="index"
                        >
                         <span>{{vo.classifyName}}</span>
                        </li>
                    </ul>
                </div>
                <div class="unfinishedanswer-right">
                        <el-table :data="tableData"  stripe style="width: 100%" v-loading="loading">
                           <el-table-column label="问卷名称" >
                                <template slot-scope="scope" :show-overflow-tooltip="true">
                                    {{ scope.row.formname}}
                                </template>
                            </el-table-column>
                            <el-table-column label="问卷类型" >
                                <template slot-scope="scope">
                                    <span>{{ scope.row.classifyname }}</span>
                                </template>
                            </el-table-column>
                            <el-table-column label="开始时间">
                                <template slot-scope="scope">
                                    <span>{{ scope.row.starttime | time}}</span>
                                </template>
                            </el-table-column>
                            <el-table-column label="结束时间">
                                <template slot-scope="scope">
                                    <span>{{ scope.row.endtime | time}}</span>
                                </template>
                            </el-table-column>
                            <el-table-column label="操作" width="160">
                                <template slot-scope="scope">
                                    <el-button type="text" size="small" @click="handlelock( scope.row)">
                                        <svgIcon icon-class="iconbianji1"></svgIcon>&nbsp;&nbsp;开始答卷
                                    </el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page.page" :page-sizes="[15, 30, 40, 50]" :page-size="page.rows"  layout="prev, pager, next, jumper, sizes, total" :total="total"></el-pagination>
                </div>
        </el-row>
    </div>
</template>
<script>
import {formatDate} from '@/utils/utils.js'
export default {
    data(){
        return{
            loading: false,
            sideActiveIndex: 0,
            tableData:[],
            total:0,
            page:{           
                classifyId:'',
                page:1,
                rows:15
            },
            classifyOptions:[],
            api:{
                listUnFinish:'/form/rest/FormMainBaseBusiness/listUnFinish/v1',
                classify:'/form/rest/FormClassifyBusiness/listAllForStu/v1',
            }
        }
    },
    mounted(){
      this.classifyAll()  
    },
    methods:{      
         classifyAll(){
            this.$post(this.api.classify).then(res=>{
                if(res.code ===200){
                    this.classifyOptions=res.data || []
                    this.page.classifyId= this.classifyOptions[0].id
                    this.initData()
                }
            })
        },
        initData() {
            this.$post(this.api.listUnFinish, this.page).then(res => {
                if (res.code === 200) {            
                    this.tableData = res.data.rows
                    this.total = res.data.total
                }
                 this.loading = false
            })
        },
        handlelock(row){
            this.$router.push({path:'/Iaminvolved/answer',query:{id:row.formid, userId:row.userid}})
        },
        sideClick(id,index){
            this.sideActiveIndex = index;
            this.page.classifyId=id
            this.page.page=1
            this.page.rows=15
            this.initData()
        },
         handleSizeChange(val) {
            this.page.rows = val;
            this.initData()
        },
        handleCurrentChange(val) {
            this.page.page = val;
            this.initData();
        }
    },
    filters:{
     time(val){
         return val ? formatDate(val, 'yyyy-MM-dd') : ''     
     }
  }
}
</script>
<style lang="scss">
    @import '../../styles/center.scss';
</style>
<style lang="scss" scoped>
    .unfinishedanswer{
        .active{
            background-color: #5f77ff;
            color: #fff;
            border-radius: 4px;
            border:0;
        }
        .ability-query_side{
            width:260px;
            min-height:100%;
            overflow-y: auto;
            background:#fff;
            border-radius:10px;
            float:left;
            padding-bottom:80px;
            h3{
               line-height:80px;
               text-align:center;
                color:#323232;
                font-size:20px;
            }
            ul{
                li{
                    height:60px;  
                    width:100%;      
                    position:relative;   
                    background:#fff;    
                     cursor:pointer;  
                    span{
                        display:inline-block;
                        width:160px;
                        height:40px;
                        font-size:14px;      
                        line-height:40px;
                        text-align:center;
                        background:#fff;
                        color:#696969;
                        border-radius:20px;
                        white-space: nowrap;     
                        overflow: hidden;         
                        text-overflow: ellipsis;  
                        padding:0 20px;             
                         position:absolute;
                         left:0;
                         top:0;
                         right:0;
                         bottom:0;
                         margin:auto;

                    }
                    &.sideActive{
                        span{
                            background:#03B187;
                            box-shadow:0px 2px 20px 0px rgba(3,177,135,0.3);
                            color:#fff;
                            font-size:16px;
                            
                        }    
                    }
                }
            
            }
        }
        .unfinishedanswer-right{
            width:calc(100% - 300px);
            overflow-y: auto;
            float:right;
            margin-right:20px;
        }
        .el-pagination{
            margin:20px 0;
            float: right;
        }
        
    }
</style>



